@use "utils/variables" as v;

.menu__logo{
    width: 36px;
}
.play{
    display: none;
}

.charts-ctn{
    display: none;
}

.cta--close{
    display: none;
}

body[data-intro]{
  .play{
      overflow-x: hidden;
      display: block;
      width: 100%;

       &__img{
        width: 100%;
        max-height: 200px;
        position: relative;
        left: -50%;
        object-fit: contain;


        &--left{
            top: 2rem;
        }
        &--right{
            top: 2rem;
        }
    }

    &__ctn__model{
        margin-top: 3rem;
        display: flex;
        justify-content: flex-start;
        position: relative;

        &__text{
            position: absolute;
            top: 1.5rem;
            right: 6.4vw;
            z-index: 1;
            text-align: right;
        }
    }

    &__buttons{
        display: flex;
        justify-content: center;
        margin-top: 4.5rem;
    }
  }
}

body[data-compare]{
    .play{
    &__ctn{
        padding-bottom: 2rem;
        
        &__model{
            &__text{
                display: none;
            }

            &--right{
                transform: scaleX(-1);
                right: -50%;  
                margin-top: -130px; 
            }
        }
    }

    &__img{
        max-height: 125px;
        left: 0%;      

        &--left{
            left: -50%;
        }
    }

    &__buttons{
        display: flex;
        justify-content: center;
        margin-bottom: 3rem;

        &__select{
            display: none;
        }
        &__compare{
            display: flex;
            flex-direction: column;
        }
        .cta--close{
            display: block;
        }
        .cta--comparer{
            display: none;
        }
        
      }
    }

    .charts-ctn{
        display: block;
        position: absolute;
        bottom: 0%;
        width: 100%;
        height: 40vh;
        background-color: white;
    }

    .charts{
        overflow-y: scroll;
        height: 100%;
    }
}


@media (min-width: 500px){

    body[data-intro] {

        
    .play__img--right{
        transform: scaleX(-1);
    }

    .play{
        &__ctn{
            margin-top: 20vh;
            padding: 0 6.4vw;
            display: flex;
            justify-content: center;
            align-items: center;
            position: initial;
            flex-direction: row;

            &__model{
                width: 100%;
                display: flex;
                flex-direction: column;

                &--right{
                    padding-left: 6.4vw;
                }
            }
        }
        
        &__img{
            position: initial;
        }
    }

        .play__ctn__model__text{
            position: initial;
            text-align: center;
            margin-bottom: 3rem;
            
            &--right{
                text-align: center;
            }
        }
    }

    body[data-compare] {
        overflow-y: hidden;

        .play__ctn{
            margin-top: 0vh;
        }
        .play__ctn__model--right{
            -webkit-transform: none; 
            transform: none; 
            right: 0; 
            margin-top: 4rem;
        }

        .play__ctn__model__text{
           display: block;
        }
}
}

@media (min-width: 1024px){

    body[data-compare] {
        .play{
            &__ctn{
                margin-top: 2vh;
            }
        }
        .charts-ctn{
            overflow-y: hidden;
            height: 25vh;
            width: 100%;  
            padding-bottom: 3rem; 
        }
    }
 }