@use "utils/variables" as v;

.introduction{
    width: 100vw;
    height: calc(100vh - 3rem);
    left: 0;
    background-color: var(--color-bg);
    position: relative;
    z-index: 4;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &__content{
        max-width: 416px;
        padding: 3rem 6.4vw;

        > *{
            max-width: 299px;
        }
        &__mg{
            margin-bottom: 3rem;
        }
    }   
}

body[data-intro] .introduction{
    display: none;
} 

@media (min-width: 500px){
    .introduction{
        align-items: flex-end;
        
        &__content{
            > *{
                max-width: 347px;
            }
        }   
    }
}


@media (min-width: 500px){
    .introduction{
        align-items: flex-end;

        &__content{
            > *{
                max-width: 416px;
            }
        }   
    }
}